import React, {useEffect} from 'react';
import { Link } from 'react-router-dom';
import $ from 'jquery';


import ArtProjectIntro from '../ArtProjectIntro';
import ArtProjectImg from '../ArtProjectImg';
import ArtProjectConcept from '../ArtProjectConcept';
import Button from '../../../UI/Button';
import NavRow from '../../../UI/navRow';

import ImgFickle1 from '../../../../assets/fickleStructure/img/diptico-mano.jpg';
import ImgFickle2 from '../../../../assets/fickleStructure/img/busqueda-de-un-equilibrio.jpg';
import ImgFickle3 from '../../../../assets/fickleStructure/img/articulacion-de-lo-subjetivo.jpg';
import ImgFickle4 from '../../../../assets/fickleStructure/img/indicios-de-una-posibilidad.jpg';
import ImgFickle5 from '../../../../assets/fickleStructure/img/triangulo3.jpg';
import ImgFickle6 from '../../../../assets/fickleStructure/img/lo-maleable.jpg';
import ImgFickle7 from '../../../../assets/fickleStructure/img/la-grieta.jpg';
import ImgFickle8 from '../../../../assets/fickleStructure/img/diptico-mano-ojo.jpg';
import ImgFickle9 from '../../../../assets/fickleStructure/img/diptico-triangulo.jpg';
import ImgFickle10 from '../../../../assets/fickleStructure/img/simetria-aparente.jpg';
import ImgFickle11 from '../../../../assets/fickleStructure/img/lineas-de-fuga.jpg';
import classes from './FlickeStructure.module.css';

// import Auxi from '../../hoc/Auxi';



const FickleStructure = () => {

        useEffect(() => {

                $(document).ready(function () {
                        $("#arrow2").click(function () {
                            $("#content1").scrollLeft($("#content1").scrollLeft()+600);
                        });
                    });
                    $("#arrow1").click(function () {
                        $("#content1").scrollLeft($("#content1").scrollLeft()-600);
                    });
                
                $(document).ready(function () {
                        $("#arrow4").click(function () {
                            $("#content2").scrollLeft($("#content2").scrollLeft()+600);
                        });
                    });
                    $("#arrow3").click(function () {
                        $("#content2").scrollLeft($("#content2").scrollLeft()-600);
                    });
              });

return (

<div className="container-fluid">
        <div className="row">
                <ArtProjectIntro 
                        title='Fickle Structure'
                        description='The project “Fickle structure” explores 
                        the contrast between different concepts that characterize 
                        the contemporary social condition. The contradiction between 
                        limit/overflow, fluid/static, liquid/solid, organic/geometric, 
                        stoic/emotional, duration/acceleration. The contrast between 
                        a structured, limited and stereotyped existence and an enduring 
                        seek towards a genuine and free way of living.' />
        </div> 
                <div className="row">
                        <NavRow idOne="arrow1" idTwo="arrow2" />
                </div>

<div className={classes.FlickeStructure} id='content1'>
                        <ArtProjectImg 
                                src={ImgFickle1}
                                alt='diptico-mano'
                                imgDesc='Diptych, 70x110 cm.
                                Mixed media on canvas. 2019.' />
                        
                        <ArtProjectImg 
                                src={ImgFickle2} 
                                alt='equilibrio'
                                imgDesc='120x100 cm.
                                Mixed media on canvas.
                                2018.' />
                        <ArtProjectImg 
                                src={ImgFickle3} 
                                alt='subjetivo'
                                imgDesc='Diptych, 70x130 cm.
                                Mixed media on canvas.
                                2018.' />
                        <ArtProjectImg 
                                src={ImgFickle4} 
                                alt='posibilidad'
                                imgDesc='70x130 cm.
                                Mixed media on canvas.
                                2018.' />
                        <ArtProjectImg 
                                src={ImgFickle5} 
                                alt='triangulo'
                                imgDesc='70x70 cm.
                                Mixed media on canvas.
                                2018.' />
                        <ArtProjectImg 
                                src={ImgFickle6} 
                                alt='maleable'
                                imgDesc='100x150 cm.
                                Enamel on canvas.
                                2018.' />
                        <ArtProjectImg 
                                src={ImgFickle7} 
                                alt='grieta'
                                imgDesc='100x100 cm.
                                Enamel on canvas.
                                2018.' />                            
</div>

                <div className="row">
                        <NavRow idOne="arrow3" idTwo="arrow4"/>
                </div>
    
        <div className={classes.FlickeStructure} id='content2'>
                <ArtProjectImg 
                        src={ImgFickle8}
                        alt='diptico-mano'
                        imgDesc='Diptych, 65x90 cm.
                        Mixed media on paper.
                        2019.' />
                
                <ArtProjectImg 
                        src={ImgFickle9} 
                        alt='equilibrio'
                        imgDesc='Diptych, 65x90 cm.
                        Mixed media on paper.
                        2019.' />
                <ArtProjectImg 
                        src={ImgFickle10} 
                        alt='subjetivo'
                        imgDesc='Diptych, 65x90 cm.
                        Mixed media on paper.
                        2019.' />
                <ArtProjectImg 
                        src={ImgFickle11} 
                        alt='posibilidad'
                        imgDesc='Diptych, 180x200 cm.
                        Mixed media on paper.
                        2019.' />
        </div>

        <div>
                <ArtProjectConcept 
                        title='Concept'
                        description='Zygmunt Bauman’s concept of “liquidity” defines the liquid condition as a characteristic of the present 
                        phase of modernity The metaphor of liquidity portrays the essence of the world we live in: its rapidity, permeability 
                        and mutability. The digital era provides more information than the human brain can handle. Regarding the social spheres, 
                        society cannot prevent being carried away by a great flood which, precisely because of its liquid form, can overcome all 
                        resistance and engulf us. We can only “react” to the liquefaction of the world, but not “act” independently, and that is 
                        what makes humankind uncertain and dissatisfied.

                        “What makes modernity “liquid” is the unstoppably accelerating “modernization” through which – just like other liquids – 
                        no forms of social life are able to retain their shapes for long. “Melting of solids,” an endemic and defining feature of 
                        all modern forms of life, continues, but melted solids are no longer intended, as before, to be replaced by “new and improved,” 
                        “more solid” solids, no longer hoped to be immune to further melting.” (Zygmunt Bauman, "Liquid Modernity")
                        
                        "In a liquid modern life there are no permanent bonds, and any that we take up for a time must be tied loosely so that 
                        they can be untied again, as quickly and as effortlessly as possible, when circumstances change - as they surely will 
                        in our liquid modern society, over and over again." (Zygmunt Bauman, "Liquid Modernity")
                        
                        How do we evolve from a “corrosion and slow disintegration of community" into a way of "living together differently"?' />
        </div>
        <div >
                <Link to='/' ><Button>Previous</Button></Link>
                <Link to='/controversy' ><Button float='right'>Next</Button></Link>
        </div>
</div>

);
}

export default FickleStructure;
