import React from 'react';

import classes from './DesignProjects.module.css';
import Project6 from '../../../assets/images/chroma.jpg'
import Project7 from '../../../assets/images/clownia.jpg'
import Project8 from '../../../assets/images/pensarte.jpg'
import Project9 from '../../../assets/images/senses.jpg'
import Project10 from '../../../assets/images/clarice.jpg'
import Project11 from '../../../assets/images/mont1.jpg'


const designProjects = (props) => (
  <div className={classes.DesignProjects}>
      <h1 id='designProjects'>Design Projects</h1>
      <div class="row margen3 container-fluid">
              <div className="col-12 col-md-4 margenitem move2">
                  <img src={Project6} alt="p6" width="100%" height="auto" onClick={props.projected}/>
                  <span>CHROMA Festival</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                  <img src={Project7} alt="p7" width="100%" height="auto" />
                  <span>Clownia Festival</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project8} alt="p8" width="100%" height="auto" />
                <span>Pensarte Magazine</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project9} alt="p9" width="100%" height="auto" />
                <span>Senses</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project10} alt="p10" width="100%" height="auto" />
                <span>Clarice Lispector</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project11} alt="p11" width="100%" height="auto" />
                <span>Fundamental Stone</span>
              </div>
          </div>
  </div>
   

);

export default designProjects;