import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom'; 

import Layout from './components/Layout/Layout';
import HomePage from './components/HomePage/HomePage';
import FickleStructure from './components/Projects/ArtProjects/FickleStructure/FickleStructure';
import Controversy from './components/Projects/ArtProjects/Controversy/Controversy';
import Auxi from './hoc/Auxi';
import classes from './App.module.css';

import 'bootstrap/dist/css/bootstrap.min.css';

class App extends Component {
   

  render () {
      return (
        <Auxi className={classes.App}>
            <BrowserRouter>
                <Layout>
                    <Switch>
                        <Route path='/ficklestructure' exact component={FickleStructure} />     
                        <Route path='/Controversy' exact component={Controversy} />
                        <Route path='/' component={HomePage} />                      
                    </Switch>
                </Layout>  
            </BrowserRouter>
        </Auxi>
      );
  }

}

export default App;
