import React, { Component } from 'react';

import EjemploMano1 from '../../../assets/images/mano1.png';
import EjemploMano2 from '../../../assets/images/mano2.png';
import EjemploMano3 from '../../../assets/images/mano3.png';
import classes from './CoverPage.module.css';

class CoverPage extends Component {
    state = {
        showHand: true,
    }
    
    toggleHandHandler = () => {
        const doesShow = this.state.showHand;
        this.setState({showHand: !doesShow});
    }

    render() {
        return (
            <div className={classes.CoverPage}>
            {this.state.showHand ? 
                <div>
                    <img src={EjemploMano1} alt="Mano1" onClick={this.toggleHandHandler} />
                    <img src={EjemploMano2} alt="Mano2" onClick={this.toggleHandHandler} />
                    <img src={EjemploMano3} alt="Mano3" onClick={this.toggleHandHandler} /> 
                </div> : 
                <div>
                    <img src={EjemploMano2} alt="Mano2" onClick={this.toggleHandHandler} />
                    <img src={EjemploMano3} alt="Mano3" onClick={this.toggleHandHandler} /> 
                    <img src={EjemploMano1} alt="Mano1" onClick={this.toggleHandHandler} />
                </div> 
            }
            </div> 
        );
    }
}

export default CoverPage;

