import React from 'react';

import classes from './ArtProjectImg.module.css';

const artProjectImg = (props) => (

    <div className={classes.ArtProjectImg}>
        <div className='card' style={{border: 'none'}}>
                <img src={props.src} alt={props.alt} width="auto" height="780px" />
                <span>{props.imgDesc}</span>
        </div>
    </div>


);

export default artProjectImg;