import React from 'react';
import { Link } from 'react-router-dom';

import classes from './ArtProjects.module.css';
import Project1 from '../../../assets/images/estructurav-1.png';
import Project2 from '../../../assets/images/controversia1-1.jpg';
import Project3 from '../../../assets/images/instancia1.jpg';
import Project4 from '../../../assets/images/femininity.jpg';
import Project5 from '../../../assets/images/portrait1.jpg';


const artProjects = (props) => (
  <div className={classes.ArtProjects}>
      <h1 id={'artProjects'}>Art Projects</h1>
      <div className="row margen3 container-fluid">
              <div className="col-12 col-md-4 margenitem move2">
                  <Link to='/ficklestructure'><img src={Project1} alt="p1" width="100%" height="auto" onClick={props.projected}/></Link>
                  <span>Fickle Structure</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                  <Link to='/controversy'><img src={Project2} alt="p2" width="100%" height="auto" /></Link>
                  <span>Limit/Overflow of Controversy</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project3} alt="p3" width="100%" height="auto" />
                <span>Glare Instance</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project4} alt="p4" width="100%" height="auto" />
                <span>Femininity</span>
              </div>
              <div className="col-12 col-md-4 margenitem move2">
                <img src={Project5} alt="p5" width="100%" height="auto" />
                <span>Portraits</span>
              </div>
          </div>
  </div>
   

);

export default artProjects;