import React from 'react';

import classes from './ArtProjectIntro.module.css'

const artProjectIntro = (props) => (
    <div className={classes.ArtProjectIntro}>
        <div className="col-lg-12 offset-xl-3 col-xl-6" >
                <h2>{props.title}</h2>
                <p>{props.description}</p>
        </div>
    </div>
);

export default artProjectIntro;